x;\n            gap: 5px;\n            justify-content: space-between;\n            background-color: white;\n\n            .main-info {\n              min-width: 82%;\n            }\n            padding: 10px;\n\n            .project-links {\n              display: flex;\n              flex-basis: auto;\n              align-items: center;\n              justify-content: end;\n              gap: 15px;\n              img {\n                max-width: 2.2em;\n                min-width: 1em;\n              }\n            }\n          }\n        }\n      }\n    }\n  }\n}\n","@import \"helpers\";\n\nfooter {\n  background-color: $secondary-light;\n  .info-container {\n    display: flex;\n    flex-wrap: wrap;\n    justify-content: center;\n    column-gap: 200px;\n    row-gap: 20px;\n\n    .info {\n      display: flex;\n      flex-direction: column;\n      row-gap: 30px;\n\n      ul {\n        li {\n          display: flex;\n          gap: 10px;\n          align-items: center;\n        }\n        span img {\n          max-width: 2em;\n        }\n      }\n\n      .social-media {\n        display: flex;\n        justify-content: center;\n        gap: 15px;\n        img {\n          max-width: 4em;\n        }\n      }\n    }\n\n    div {\n      max-width: 400px;\n    }\n  }\n}\n","@import \"reset\";\n@import \"header\";\n@import \"mainSection\";\n@import \"myProjects\";\n@import \"footer\";\n\n* {\n  font-family: \"Roboto\";\n  scroll-behavior: smooth !important;\n}\n\nimg {\n  width: 100%;\n  display: block;\n}\n\nsection {\n  padding: $header-height 20px;\n}\n\nh1 {\n  font-size: 2.25em;\n}\n"],"sourceRoot":""}]);
